/* flex helpers */
.is-stretched
    flex: 1

.is-between
    justify-content: space-around
    flex-wrap: wrap

.is-col-flexed
    display: flex
    flex-direction: column
    flex-flow: column
    flex-wrap: wrap

.is-row-flexed
    display: flex
    flex-direction: row
    flex-flow: row
    flex-wrap: wrap

.push-to-start
    align-items: flex-start
    margin-top: 2rem

.center-self
    align-self: center

.is-centered-flex
    justify-content: center
    align-items: center

.is-flex > figure
    background-color: $black
    min-height: 12rem
    min-width: 20rem

    margin: 0.5rem
    justify-content: flex-start

.is-aligned-center
    align-self: center
.is-flexed-to-start
    justify-self: flex-start

/* margins and padding helpers */
.has-margin-top
    margin-top: 2rem

.has-margin
    margin: 1rem

.section > figure
    padding: 1rem

.lang-info
    max-width: 10rem
    padding-left: 1rem


.has-padding
    padding: 1rem

/* Font helpers */
.is-serif
    font-family: $family-serif

.is-mono-font
    font-family: 'PT Mono', monospace


/* display helpers*/
.is-sticky
    position: sticky
    top: 5%

@media screen and (max-width: 768px)
    .is-overflow
        margin-top: 1.5rem
        max-height: 120vmin
        overflow-y: scroll
        -ms-overflow-style: none
        scrollbar-width: none
        scroll-behavior: smooth
        &::-webkit-scrollbar
            display: none

.disable-selection
    -moz-user-select: none 
    -ms-user-select: none
    -khtml-user-select: none 
    -webkit-user-select: none
    -webkit-touch-callout: none


/* custom paginations
.pagination-link
    background-color: $black
    border-color: $light
    color: $light
    &:hover
        background-color: $light
        border-color: $black
        color: $dark
.pagination-link.is-current
    background-color: $dark
    &:hover
        color: $white

.pagination-next
    border-color: $light
    &:hover
        background-color: $black

/* decorators */
.has-background-dark-green
    background-color: $dark-green

.has-radius-img
    border-radius: 10rem
    margin: 1rem 1rem 1rem


.has-radius
    border-radius: 3rem

.has-slight-radius
    border-radius: 0.7rem

.img-size
    width: 200px

.shadowed
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75)
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75)
    box-shadow: 5px 3px 5px 0px rgba(0,0,0,0.75)

.is-shadow-top
    -webkit-box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)
    -moz-box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)
    box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)

/* font helpers */
*
    font-size: 16px

p, span, code, pre
    color: $white
    font-size: 1rem

pre
    background-color: #282c34


.navbar-end > .navbar-item
    font-weight: 1000

i
    color: $light

.foot-links > .foot-link
    color: $light
    font-weight: 800

.crumbs > .crumb-link
    color: $light
    font-weight: 600

.is-lang
    font-size: 1.1rem
    max-width: 8rem
    font-family: $family-serif
    color: $light


.column.is-one-fifth
    @media screen and (max-width: 909px)
        width: 0%
        display: none
        visibility: hidden
    @media screen and(max-width: 768px)
        display: block
        visibility: visible
        width: 100%